import React, { useContext, useState } from 'react';
import {
  Flex,
  Text,
  IconButton,
  Divider,
  Avatar,
  Heading,
  useColorModeValue,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
  Icon,
} from '@chakra-ui/react';
import { FiMenu, FiLogOut } from 'react-icons/fi';
import {
  MdMovie,
  MdGames,
  MdPlaylistAdd,
  MdFormatListBulleted,
} from 'react-icons/md';
import { FaUserAlt, FaKey } from 'react-icons/fa';
import NavItem from './NavItem';
import Cookies from 'js-cookie';
import { AuthContext } from '../../store/AuthContext';
import { useHistory } from 'react-router-dom';

export default function Sidebar() {
  const user = Cookies.get('user');
  const { setLoginStatus } = useContext(AuthContext);
  const sidebarCollapsed = localStorage.getItem('sidebar-collapsed');
  const [isExpanded, setIsExpanded] = useState(sidebarCollapsed ? false : true);
  const bg = useColorModeValue('gray.100', 'gray.900');
  let history = useHistory();
  const handleLogout = () => {
    setLoginStatus(false);
    Cookies.remove('email');
    Cookies.remove('user');
    Cookies.remove('token');
    history.push('/sign-in');
  };

  const handleToggler = () => {
    if (isExpanded) {
      setIsExpanded(false);
      localStorage.setItem('sidebar-collapsed', true);
      return;
    }
    setIsExpanded(true);
    localStorage.removeItem('sidebar-collapsed');
  };

  return (
    <Flex
      pos="sticky"
      left="5"
      h="500px"
      my="2.5vh"
      boxShadow="0 4px 12px 0 rgba(0, 0, 0, 0.05)"
      borderRadius={!isExpanded ? '15px' : '20px'}
      w={!isExpanded ? '80px' : '250px'}
      flexDir="column"
      justifyContent="space-between"
      bg={bg}
      transition="0.3s"
    >
      <Flex
        p="5%"
        flexDir="column"
        w="100%"
        alignItems={!isExpanded ? 'center' : 'flex-start'}
        as="nav"
      >
        <IconButton
          background="none"
          mt={5}
          size="lg"
          _hover={{ background: 'none' }}
          icon={<FiMenu />}
          onClick={handleToggler}
        />
        <Accordion
          transition="0.3s"
          allowMultiple
          w={!isExpanded ? '80px' : '220px'}
        >
          <AccordionItem>
            <h2>
              <AccordionButton>
                <Flex alignItems="center" flex="1" textAlign="left">
                  <Icon as={MdMovie} fontSize="xl" />
                  <Text
                    ml={5}
                    textAlign="left"
                    display={!isExpanded ? 'none' : 'flex'}
                  >
                    Movies
                  </Text>
                </Flex>
                <AccordionIcon />
              </AccordionButton>
            </h2>
            <AccordionPanel pb={2}>
              <NavItem
                navSize={isExpanded}
                icon={MdFormatListBulleted}
                title="Movie List"
                path="/movie/list"
              />
              <NavItem
                navSize={isExpanded}
                icon={MdPlaylistAdd}
                title="Add New Movie"
                path="/movie/add"
              />
            </AccordionPanel>
          </AccordionItem>

          <AccordionItem>
            <h2>
              <AccordionButton>
                <Flex alignItems="center" flex="1" textAlign="left">
                  <Icon as={MdGames} fontSize="xl" />
                  <Text
                    ml={5}
                    textAlign="left"
                    display={!isExpanded ? 'none' : 'flex'}
                  >
                    Games
                  </Text>
                </Flex>
                <AccordionIcon />
              </AccordionButton>
            </h2>
            <AccordionPanel pb={2}>
              <NavItem
                navSize={isExpanded}
                icon={MdFormatListBulleted}
                title="Game List"
                path="/game/list"
              />
              <NavItem
                navSize={isExpanded}
                icon={MdPlaylistAdd}
                title="Add New Game"
                path="/game/add"
              />
            </AccordionPanel>
          </AccordionItem>

          <AccordionItem>
            <h2>
              <AccordionButton>
                <Flex alignItems="center" flex="1" textAlign="left">
                  <Icon as={FaUserAlt} fontSize="xl" />
                  <Text
                    ml={5}
                    textAlign="left"
                    display={!isExpanded ? 'none' : 'flex'}
                  >
                    Account
                  </Text>
                </Flex>
                <AccordionIcon />
              </AccordionButton>
            </h2>
            <AccordionPanel pb={2}>
              <NavItem
                navSize={isExpanded}
                icon={FaKey}
                title="Change Password"
                path="/change-password"
              />
              <NavItem
                navSize={isExpanded}
                icon={FiLogOut}
                title="Logout"
                path="#"
                onClick={handleLogout}
              />
            </AccordionPanel>
          </AccordionItem>
        </Accordion>
      </Flex>

      <Flex
        p="5%"
        flexDir="column"
        w="100%"
        alignItems={!isExpanded ? 'center' : 'flex-start'}
        mb={4}
      >
        <Divider display={!isExpanded ? 'none' : 'flex'} />
        <Flex mt={4} align="center" ml={!isExpanded ? 0 : 2}>
          <Avatar size="sm" src="avatar-1.jpg" />
          <Flex flexDir="column" ml={4} display={!isExpanded ? 'none' : 'flex'}>
            <Heading as="h3" size="sm" wordBreak="break-word">
              {user}
            </Heading>
          </Flex>
        </Flex>
      </Flex>
    </Flex>
  );
}
