import React, { useState, createContext, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import {
  authRegister,
  authLogin,
  authChangePassword,
} from '../services/authApi';
import Cookies from 'js-cookie';
import { useToast } from '@chakra-ui/react';

export const AuthContext = createContext();

export const AuthProvider = props => {
  const [loginStatus, setLoginStatus] = useState(false);
  let history = useHistory();
  const toast = useToast();

  useEffect(() => {
    if (Cookies.get('token') !== undefined) {
      setLoginStatus(true);
    }
  }, []);

  const userRegister = userData => {
    authRegister(userData)
      .then(() => {
        history.push('/sign-in');
        toast({
          title: 'Registration successful',
          description: `Your account was created successfully `,
          status: 'success',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
      })
      .catch(err => {
        toast({
          title: 'Sign Up failed.',
          description: 'Please fill in all the fields correctly.',
          status: 'error',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
      });
  };

  const userLogin = userData => {
    authLogin(userData)
      .then(res => {
        let user = res.data.user;
        let token = res.data.token;
        Cookies.set('user', user.name, { expires: 1 });
        Cookies.set('email', user.email, { expires: 1 });
        Cookies.set('token', token, { expires: 1 });
        history.push('/');
        setLoginStatus(true);
        toast({
          title: 'Login successful',
          description: `Welcome, ${user.name}`,
          status: 'success',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
      })
      .catch(err => {
        toast({
          title: 'Login failed.',
          description: 'Email or password is not correct .',
          status: 'error',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
      });
  };

  const userChangePassword = userData => {
    authChangePassword(userData)
      .then(() => {
        history.push('/');
        toast({
          title: 'Change password successful.',
          description: `Your password has been changed successfully `,
          status: 'success',
          duration: 3000,
          isClosable: true,
          position: 'top-right',
        });
      })
      .catch(err => {
        toast({
          title: 'Change password failed.',
          description: 'Please check your current password',
          status: 'error',
          duration: 3000,
          isClosable: true,
          position: 'top-right',
        });
      });
  };

  return (
    <AuthContext.Provider
      value={{
        loginStatus,
        setLoginStatus,
        userRegister,
        userLogin,
        userChangePassword,
      }}
    >
      {props.children}
    </AuthContext.Provider>
  );
};
