import {
  Button,
  Flex,
  FormControl,
  FormLabel,
  Heading,
  Input,
  Stack,
  useColorModeValue,
  Text,
  useToast,
} from '@chakra-ui/react';
import { useContext, useState } from 'react';
import { AuthContext } from '../../store/AuthContext';
import Sidebar from '../Sidebar/Sidebar';

export default function ChangePassword() {
  const { loginStatus, userChangePassword } = useContext(AuthContext);
  const [input, setInput] = useState({
    current_password: '',
    new_password: '',
    new_confirm_password: '',
  });

  let toast = useToast();

  const handleChange = event => {
    let value = event.target.value;
    let name = event.target.name;

    setInput({ ...input, [name]: value });
  };

  const handleSubmit = event => {
    event.preventDefault();
    if (input.new_confirm_password !== input.new_password) {
      toast({
        title: 'Error.',
        description: 'Your new password doesnt match',
        status: 'error',
        duration: 3000,
        isClosable: true,
        position: 'top-right',
      });
      return;
    }
    if (!input.new_confirm_password !== input.new_password) {
    }
    userChangePassword({
      current_password: input.current_password,
      new_password: input.new_password,
      new_confirm_password: input.new_confirm_password,
    });
  };
  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Flex
        mt={30}
        mx="auto"
        w={'lg'}
        align={'center'}
        justify={'center'}
        bg={useColorModeValue('white', 'gray.800')}
        data-aos="zoom-in"
      >
        <Stack
          spacing={4}
          w={'full'}
          maxW={'md'}
          bg={useColorModeValue('white', 'gray.700')}
          rounded={'xl'}
          boxShadow={'lg'}
          p={6}
          my={12}
        >
          <Heading lineHeight={1.1} fontSize={{ base: '2xl', md: '3xl' }}>
            Enter new password
          </Heading>
          <Text fontSize="sm" color="darkgrey">
            Password at least have 8 characters
          </Text>
          <FormControl id="current_password" isRequired>
            <FormLabel>Current Password</FormLabel>
            <Input
              type="password"
              name="current_password"
              onChange={handleChange}
              value={input.current_password}
              shadow="md"
            />
          </FormControl>
          <FormControl id="new_password" isRequired>
            <FormLabel>New Password</FormLabel>
            <Input
              type="password"
              name="new_password"
              onChange={handleChange}
              value={input.new_password}
              shadow="md"
            />
          </FormControl>
          <FormControl id="new_confirm_password" isRequired>
            <FormLabel>Confirm New Password</FormLabel>
            <Input
              type="password"
              name="new_confirm_password"
              onChange={handleChange}
              value={input.new_confirm_password}
              shadow="md"
            />
          </FormControl>
          <Stack spacing={6}>
            <Button
              bg={'blue.400'}
              shadow="md"
              color={'white'}
              _hover={{
                bg: 'blue.500',
              }}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </Stack>
        </Stack>
      </Flex>
    </Flex>
  );
}
