import {
  chakra,
  Box,
  Flex,
  Image,
  Badge,
  useColorModeValue,
  Text,
  Icon,
  Tab,
  Tabs,
  TabList,
  TabPanels,
  TabPanel,
} from '@chakra-ui/react';
import { useContext, useState, useEffect } from 'react';
import { StarIcon, TimeIcon } from '@chakra-ui/icons';
import { useParams } from 'react-router-dom';
import { getMovie } from '../../services/moviesApi';
import { AuthContext } from '../../store/AuthContext';
import Sidebar from '../Sidebar/Sidebar';

const MovieDetails = () => {
  const { loginStatus } = useContext(AuthContext);
  const [movieDetails, setMovieDetails] = useState([]);
  const { Id } = useParams();

  useEffect(() => {
    const getMovieDetails = async () => {
      const data = await getMovie(Id);
      setMovieDetails(data);
    };

    getMovieDetails();
  }, []);

  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Box mx="auto" data-aos="zoom-out">
        <Flex w="1000px" mx="auto" my={8} rounded="lg">
          <Box mr={4} maxW="400px">
            <Image
              rounded="lg"
              objectFit="cover"
              w="400px"
              maxH="600px"
              src={movieDetails.image_url}
              alt={movieDetails.title}
            />
          </Box>
          <Box p={4} w="650px">
            <chakra.h1 fontSize="3xl" fontWeight="bold">
              {`${movieDetails.title} (${movieDetails.year})`}
            </chakra.h1>
            <Flex alignItems="center">
              <Badge colorScheme="blue" fontSize="0.8em" mr={1}>
                {movieDetails.genre}
              </Badge>
              &bull;
              <Badge colorScheme="cyan" fontSize="0.8em" ml={1}>
                <TimeIcon mr={1} />
                {movieDetails.duration} Menit
              </Badge>
            </Flex>
            <Box>
              <Flex
                alignItems="center"
                mt={4}
                color={useColorModeValue('gray.700', 'gray.200')}
              >
                <Icon as={StarIcon} h={6} w={6} mr={1} />
                <chakra.h1 px={1} fontSize="lg">
                  {movieDetails.rating}/10
                </chakra.h1>
              </Flex>
            </Box>
            <Tabs mt={4} w="auto">
              <TabList>
                <Tab>Description</Tab>
                <Tab>Review</Tab>
              </TabList>

              <TabPanels>
                <TabPanel overflow="auto" h="400px">
                  <Text as="i">{movieDetails.description}</Text>
                </TabPanel>
                <TabPanel overflow="auto" h="230px">
                  <Text>{movieDetails.review}</Text>
                </TabPanel>
              </TabPanels>
            </Tabs>
          </Box>
        </Flex>
      </Box>
    </Flex>
  );
};

export default MovieDetails;
