import {
  Button,
  Heading,
  Box,
  Flex,
  Link,
  VStack,
  InputGroup,
  Input,
  InputLeftElement,
  IconButton,
  AlertDialog,
  AlertDialogBody,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogContent,
  AlertDialogOverlay,
  Select,
  Image,
} from '@chakra-ui/react';
import React, { useContext, useState, useEffect, useRef } from 'react';
import { EditIcon, DeleteIcon, AddIcon } from '@chakra-ui/icons';
import { FaCheck, FaTimes } from 'react-icons/fa';
import { MdRefresh } from 'react-icons/md';

import { AuthContext } from '../../store/AuthContext';
import Sidebar from '../Sidebar/Sidebar';
import { useHistory, Link as ReachLink } from 'react-router-dom';
import { GamesContext } from '../../store/GamesContext';
import { getGames } from '../../services/gamesApi';
import { AiOutlineSearch } from 'react-icons/ai';
import ListTable from '../ListTable/ListTable';

const GameList = () => {
  const { loginStatus } = useContext(AuthContext);
  const { deleteGameHandler, games, fetchStatus, setFetchStatus } =
    useContext(GamesContext);
  const [data, setData] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const onClose = () => setIsOpen(false);
  const cancelRef = useRef();
  const [gameId, setGameId] = useState(null);
  const [search, setSearch] = useState('');
  const [filter, setFilter] = useState({
    genre: '',
    platform: '',
    release: '',
  });
  let history = useHistory();

  useEffect(() => {
    const getGameList = async () => {
      const result = await getGames();
      setData(result);
    };
    getGameList();
  }, [fetchStatus]);

  useEffect(() => {
    setData(games.filter(item => item.name?.toLowerCase().includes(search)));
  }, [search]);

  useEffect(() => {
    const filterGame = () => {
      const filteredData = data.filter(
        item =>
          item.genre?.includes(filter.genre) &&
          item.platform?.includes(filter.platform) &&
          item.release?.includes(filter.release)
      );
      setData(filteredData);
      if (data.length < 0) {
        setFetchStatus(true);
      }
    };
    filterGame();
  }, [filter]);

  const handleSearch = event => {
    const value = event.target.value;
    setSearch(value);
  };

  const handleFilter = event => {
    const value = event.target.value;
    const name = event.target.name;
    setFilter({
      ...filter,
      [name]: value,
    });
    setData(games);
  };

  const resetFilter = () => {
    setFilter({
      genre: '',
      platform: '',
      release: '',
    });
    setData(games);
  };

  const handleDelete = () => {
    setIsOpen(false);
    deleteGameHandler(gameId);
    setFetchStatus(true);
  };

  const handleEdit = id => {
    history.push(`/game/edit/${id}`);
  };

  const columns = React.useMemo(
    () => [
      {
        Header: 'Name',
        accessor: 'name',
        Cell: ({ value }) =>
          value?.length > 25 ? `${value.substr(0, 25)}...` : value,
      },
      {
        Header: 'Image',
        accessor: 'image_url',
        Cell: ({ value }) => (
          <Image
            src={value ?? 'null'}
            alt={value}
            rounded="lg"
            objectFit="cover"
            boxSize="80px"
          />
        ),
      },
      {
        Header: 'Genre',
        accessor: 'genre',
      },
      {
        Header: 'Platform',
        accessor: 'platform',
      },
      {
        Header: 'Release',
        accessor: 'release',
      },
      {
        Header: 'Singleplayer',
        accessor: 'singlePlayer',
        Cell: ({ value }) => (value === 1 ? <FaCheck /> : <FaTimes />),
      },
      {
        Header: 'Multiplayer',
        accessor: 'multiplayer',
        Cell: ({ value }) => (value === 1 ? <FaCheck /> : <FaTimes />),
      },
      {
        Header: 'Action',
        accessor: 'id',
        Cell: ({ value }) => (
          <Flex>
            <IconButton
              colorScheme="blue"
              aria-label="edit-button"
              size="md"
              icon={<EditIcon />}
              mr={2}
              onClick={() => handleEdit(value)}
            />
            <IconButton
              colorScheme="red"
              aria-label="delete-button"
              size="md"
              icon={<DeleteIcon />}
              onClick={() => {
                setGameId(value);
                setIsOpen(true);
              }}
            />
          </Flex>
        ),
      },
    ],
    []
  );

  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Box p={8} mx="auto" w="1300px">
        <Box spacing={4}>
          <VStack spacing={4} align="stretch">
            <Flex justifyContent="space-between" alignItems="center">
              <Heading fontSize={'4xl'}>Game List</Heading>
              <Link as={ReachLink} to="/game/add">
                <Button
                  leftIcon={<AddIcon />}
                  colorScheme="blue"
                  variant="outline"
                  size="sm"
                >
                  Add new Game
                </Button>
              </Link>
            </Flex>

            <Flex justifyContent="space-between" alignItems="center">
              <Select
                placeholder="Genre"
                name="genre"
                onChange={handleFilter}
                w={300}
                value={filter.genre}
              >
                {games
                  .filter(
                    (v, i, a) => a.findIndex(t => t.genre === v.genre) === i
                  )
                  .map(item => (
                    <option value={item.genre}>{item.genre}</option>
                  ))}
              </Select>
              <Select
                placeholder="Release"
                name="release"
                onChange={handleFilter}
                w={300}
                value={filter.release}
              >
                {games
                  .filter(
                    (v, i, a) => a.findIndex(t => t.release === v.release) === i
                  )
                  .sort((a, b) => a.release - b.release)
                  .map(item => (
                    <option value={item.release}>{item.release}</option>
                  ))}
              </Select>
              <Select
                placeholder="Platform"
                name="platform"
                onChange={handleFilter}
                w={300}
                value={filter.platform}
              >
                {games
                  .filter(
                    (v, i, a) =>
                      a.findIndex(t => t.platform === v.platform) === i
                  )
                  .map(item => (
                    <option value={item.platform}>{item.platform}</option>
                  ))}
              </Select>
            </Flex>
            <Flex justifyContent="space-between">
              <InputGroup w={300}>
                <InputLeftElement
                  pointerEvents="none"
                  children={<AiOutlineSearch />}
                />
                <Input
                  type="text"
                  onChange={handleSearch}
                  placeholder="Search by game name"
                  shadow="md"
                />
              </InputGroup>
              <Button
                colorScheme="gray"
                leftIcon={<MdRefresh />}
                _focus={{ shadow: '' }}
                size="md"
                shadow="md"
                onClick={resetFilter}
              >
                Reset Filter
              </Button>
            </Flex>

            <ListTable columns={columns} data={data} />
            
          </VStack>
        </Box>
      </Box>
      <AlertDialog
        isOpen={isOpen}
        leastDestructiveRef={cancelRef}
        onClose={onClose}
      >
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              Delete Game
            </AlertDialogHeader>

            <AlertDialogBody>
              Are you sure? You can't undo this action afterwards.
            </AlertDialogBody>

            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClose}>
                Cancel
              </Button>
              <Button colorScheme="red" onClick={handleDelete} ml={3}>
                Delete
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </Flex>
  );
};

export default GameList;
