import { chakra, Box, Flex, Image, VStack, Text, Icon } from '@chakra-ui/react';
import { useContext, useState, useEffect } from 'react';
import { FaUserAlt, FaUserFriends, FaCheck, FaTimes } from 'react-icons/fa';
import { MdDevices, MdVideogameAsset } from 'react-icons/md';
import { useParams } from 'react-router-dom';
import { getGame } from '../../services/gamesApi';
import { AuthContext } from '../../store/AuthContext';
import Sidebar from '../Sidebar/Sidebar';

const GameDetails = () => {
  const { loginStatus } = useContext(AuthContext);
  const [gameDetails, setGameDetails] = useState([]);
  const { Id } = useParams();

  useEffect(() => {
    const getGameDetails = async () => {
      const data = await getGame(Id);
      setGameDetails(data);
    };

    getGameDetails();
  }, []);

  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Box mx="auto" data-aos="zoom-out">
        <Flex maxWidth="1000px" mx="auto" my={8} rounded="lg">
          <Box mr={4} maxW="400px">
            <Image
              rounded="lg"
              objectFit="cover"
              w="400px"
              h="600px"
              src={gameDetails.image_url}
              alt={gameDetails.name}
            />
          </Box>
          <Box px={4} w="650px">
            <chakra.h1 fontSize="3xl" fontWeight="bold">
              {`${gameDetails.name} (${gameDetails.release})`}
            </chakra.h1>
            <VStack
              direction="column"
              flexGrow={1}
              spacing={5}
              alignItems="start"
              mt={4}
            >
              <Flex alignItems="center">
                <Icon as={MdDevices} h={5} w={5} mr={2} />
                <Text fontWeight={700} mr={2}>
                  Platform :{' '}
                </Text>
                <Text fontWeight={400}>{gameDetails.platform}</Text>
              </Flex>

              <Flex alignItems="center">
                <Icon as={MdVideogameAsset} h={5} w={5} mr={2} />
                <Text fontWeight={700} mr={2}>
                  Genre :{' '}
                </Text>
                <Text fontWeight={400}>{gameDetails.genre}</Text>
              </Flex>

              <Flex alignItems="center">
                <Icon as={FaUserAlt} h={5} w={5} mr={2} />
                <Text fontWeight={700} mr={2}>
                  Singleplayer :{' '}
                </Text>
                <Text fontWeight={400}>
                  {gameDetails.singlePlayer === 1 ? <FaCheck /> : <FaTimes />}
                </Text>
              </Flex>

              <Flex alignItems="center">
                <Icon as={FaUserFriends} h={5} w={5} mr={2} />
                <Text fontWeight={700} mr={2}>
                  Multiplayer :{' '}
                </Text>
                <Text fontWeight={400}>
                  {gameDetails.multiplayer === 1 ? <FaCheck /> : <FaTimes />}
                </Text>
              </Flex>
            </VStack>
          </Box>
        </Flex>
      </Box>
    </Flex>
  );
};

export default GameDetails;
