import {
  Select,
  Button,
  Heading,
  Box,
  Flex,
  Link,
  VStack,
  InputGroup,
  Input,
  InputLeftElement,
  IconButton,
  AlertDialog,
  AlertDialogBody,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogContent,
  AlertDialogOverlay,
  Image,
} from '@chakra-ui/react';
import React, { useContext, useState, useEffect } from 'react';
import { EditIcon, DeleteIcon, AddIcon } from '@chakra-ui/icons';

import { AuthContext } from '../../store/AuthContext';
import { MoviesContext } from '../../store/MoviesContext';
import Sidebar from '../Sidebar/Sidebar';
import { getMovies } from '../../services/moviesApi';
import { useHistory, Link as ReachLink } from 'react-router-dom';
import { AiOutlineSearch } from 'react-icons/ai';
import { MdRefresh } from 'react-icons/md';
import ListTable from '../ListTable/ListTable';

const MovieList = () => {
  const { loginStatus } = useContext(AuthContext);
  const { movies, setFetchStatus, fetchStatus, deleteMovieHandler } =
    useContext(MoviesContext);
  const [data, setData] = useState([]);
  let history = useHistory();
  const [isOpen, setIsOpen] = React.useState(false);
  const onClose = () => setIsOpen(false);
  const cancelRef = React.useRef();
  const [movieId, setMovieId] = useState(null);
  const [search, setSearch] = useState('');
  const [filter, setFilter] = useState({
    genre: '',
    year: '',
    rating: '',
  });

  useEffect(() => {
    const getMovieList = async () => {
      const result = await getMovies();
      setData(result);
    };
    getMovieList();
  }, [fetchStatus]);

  useEffect(() => {
    setData(movies.filter(item => item.title?.toLowerCase().includes(search)));
  }, [search]);

  useEffect(() => {
    const filterMovie = () => {
      const filteredData = data.filter(
        item =>
          item.genre?.includes(filter.genre) &&
          item.year?.toString().includes(filter.year) &&
          item.rating?.toString().includes(filter.rating)
      );
      setData(filteredData);
      if (data.length < 0) {
        setFetchStatus(true);
      }
    };
    filterMovie();
  }, [filter]);

  const handleSearch = event => {
    const value = event.target.value;
    setSearch(value);
  };

  const handleFilter = event => {
    const value = event.target.value;
    const name = event.target.name;
    setFilter({
      ...filter,
      [name]: value,
    });
    setData(movies);
  };

  const resetFilter = () => {
    setFilter({
      genre: '',
      year: '',
      rating: '',
    });
    setData(movies);
  };

  const handleDelete = () => {
    setIsOpen(false);
    deleteMovieHandler(movieId);
  };

  const handleEdit = id => {
    history.push(`/movie/edit/${id}`);
  };

  const columns = React.useMemo(
    () => [
      {
        Header: 'Title',
        accessor: 'title',
        Cell: ({ value }) =>
          value?.length > 25 ? `${value.substr(0, 25)}...` : value,
      },
      {
        Header: 'Image',
        accessor: 'image_url',
        Cell: ({ value }) => (
          <Image
            src={value ?? 'null'}
            alt={value}
            rounded="lg"
            objectFit="cover"
            boxSize="80px"
          />
        ),
      },
      {
        Header: 'Description',
        accessor: 'description',
        Cell: ({ value }) =>
          value?.length > 50 ? `${value.substr(0, 50)}...` : value,
      },
      {
        Header: 'Year',
        accessor: 'year',
        isNumeric: true,
      },
      {
        Header: 'Duration',
        accessor: 'duration',
        isNumeric: true,
      },
      {
        Header: 'Genre',
        accessor: 'genre',
      },
      {
        Header: 'Rating',
        accessor: 'rating',
        isNumeric: true,
      },
      {
        Header: 'Review',
        accessor: 'review',
        Cell: ({ value }) =>
          value?.length > 50 ? `${value.substr(0, 50)}...` : value,
      },
      {
        Header: 'Action',
        accessor: 'id',
        Cell: ({ value }) => (
          <Flex>
            <IconButton
              colorScheme="blue"
              aria-label="edit-btn"
              size="md"
              icon={<EditIcon />}
              mr={2}
              shadow="md"
              onClick={() => handleEdit(value)}
            />
            <IconButton
              colorScheme="red"
              aria-label="delete-btn"
              size="md"
              shadow="md"
              icon={<DeleteIcon />}
              onClick={() => {
                setMovieId(value);
                setIsOpen(true);
              }}
            />
          </Flex>
        ),
      },
    ],
    []
  );

  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Box p={8} mx="auto" w="1300px">
        <Box spacing={4}>
          <VStack spacing={4} align="stretch">
            <Flex justifyContent="space-between" alignItems="center">
              <Heading fontSize={'4xl'}>Movie List</Heading>
              <Link as={ReachLink} to="/movie/add">
                <Button
                  leftIcon={<AddIcon />}
                  colorScheme="blue"
                  variant="outline"
                  size="sm"
                  shadow="md"
                >
                  Add new Movie
                </Button>
              </Link>
            </Flex>
            <Flex justifyContent="space-between" alignItems="center">
              <Select
                placeholder="Genre"
                name="genre"
                onChange={handleFilter}
                w={300}
                value={filter.genre}
              >
                {movies
                  .filter(
                    (v, i, a) => a.findIndex(t => t.genre === v.genre) === i
                  )
                  .map(item => (
                    <option value={item.genre}>{item.genre}</option>
                  ))}
              </Select>
              <Select
                placeholder="Year"
                name="year"
                onChange={handleFilter}
                w={300}
                value={filter.year}
              >
                {movies
                  .filter(
                    (v, i, a) => a.findIndex(t => t.year === v.year) === i
                  )
                  .sort((a, b) => a.year - b.year)
                  .map(item => (
                    <option value={item.year?.toString()}>
                      {item.year?.toString()}
                    </option>
                  ))}
              </Select>
              <Select
                placeholder="Rating"
                name="rating"
                onChange={handleFilter}
                w={300}
                value={filter.rating}
              >
                {movies
                  .filter(
                    (v, i, a) => a.findIndex(t => t.rating === v.rating) === i
                  )
                  .sort((a, b) => a.rating - b.rating)
                  .map(item => (
                    <option value={item.rating?.toString()}>
                      {item.rating?.toString()}
                    </option>
                  ))}
              </Select>
            </Flex>
            <Flex justifyContent="space-between">
              <InputGroup w={300}>
                <InputLeftElement
                  pointerEvents="none"
                  children={<AiOutlineSearch />}
                />
                <Input
                  type="text"
                  onChange={handleSearch}
                  placeholder="Search by movie title"
                  shadow="md"
                />
              </InputGroup>
              <Button
                colorScheme="gray"
                leftIcon={<MdRefresh />}
                _focus={{ shadow: '' }}
                size="md"
                shadow="md"
                onClick={resetFilter}
              >
                Reset Filter
              </Button>
            </Flex>

            <ListTable columns={columns} data={data} />
          </VStack>
        </Box>
      </Box>
      <AlertDialog
        isOpen={isOpen}
        leastDestructiveRef={cancelRef}
        onClose={onClose}
      >
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              Delete Movie
            </AlertDialogHeader>

            <AlertDialogBody>
              Are you sure? You can't undo this action afterwards.
            </AlertDialogBody>

            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClose}>
                Cancel
              </Button>
              <Button colorScheme="red" onClick={handleDelete} ml={3}>
                Delete
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </Flex>
  );
};

export default MovieList;
