import axios from 'axios';
import Cookies from 'js-cookie';

const API_ENDPOINT = `https://backendexample.sanbersy.com/api/data-movie`;

export async function getMovies() {
  const result = await axios.get(API_ENDPOINT);
  const axiosResult = result.data;

  return axiosResult;
}

export async function addMovie(movie) {
  const result = await axios.post(API_ENDPOINT, movie, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result;

  return axiosResult;
}

export async function deleteMovie(id) {
  const result = await axios.delete(`${API_ENDPOINT}/${id}`, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result;

  return axiosResult;
}

export async function getMovie(id) {
  const result = await axios.get(`${API_ENDPOINT}/${id}`);
  const axiosResult = result.data;

  return axiosResult;
}

export async function updateMovie(id, movie) {
  const result = await axios.put(`${API_ENDPOINT}/${id}`, movie, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result.data;

  console.log(axiosResult);
  return axiosResult;
}
