import axios from 'axios';
import Cookies from 'js-cookie';

const API_ENDPOINT = `https://backendexample.sanbersy.com/api/data-game`;

export async function getGames() {
  const result = await axios.get(API_ENDPOINT);
  const axiosResult = result.data;

  return axiosResult;
}

export async function addGame(game) {
  const result = await axios.post(API_ENDPOINT, game, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result;

  return axiosResult;
}

export async function deleteGame(id) {
  const result = await axios.delete(`${API_ENDPOINT}/${id}`, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result;

  return axiosResult;
}

export async function getGame(id) {
  const result = await axios.get(`${API_ENDPOINT}/${id}`);
  const axiosResult = result.data;

  return axiosResult;
}

export async function updateGame(id, game) {
  const result = await axios.put(`${API_ENDPOINT}/${id}`, game, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result.data;

  console.log(axiosResult);
  return axiosResult;
}
