import React from 'react';
import {
  Box,
  Flex,
  Image,
  useColorModeValue,
  Link,
  Tooltip,
} from '@chakra-ui/react';
import { Link as ReachLink } from 'react-router-dom';
import { StarIcon, TimeIcon, CalendarIcon } from '@chakra-ui/icons';

const MovieCard = ({ id, title, year, duration, rating, image_url }) => {
  return (
    <Flex alignItems="center" justifyContent="center">
      <Box
        bg={useColorModeValue('gray.50', 'gray.700')}
        maxW="250px"
        maxH="500px"
        borderWidth="1px"
        rounded="lg"
        shadow="lg"
        transform="scale(1.0)"
        transition="0.2s ease-in-out"
        overflow="hidden"
        _hover={{
          transform: 'scale(1.05)',
        }}
      >
        <Link
          textDecoration="none"
          _hover={{ textDecoration: 'none' }}
          as={ReachLink}
          to={`/movie/details/${id}`}
        >
          <Image
            src={image_url}
            alt={title}
            roundedTop="lg"
            objectFit="cover"
            w="300px"
            h="300px"
          />
        </Link>

        <Box p="4">
          <Box fontWeight="semibold" as="h4" lineHeight="tight" isTruncated>
            <Link as={ReachLink} to={`/movie/details/${id}`}>
              <Tooltip label={title ?? 'null'} aria-label="A tooltip">
                {title ?? 'null'}
              </Tooltip>
            </Link>
          </Box>

          <Box
            color="gray.500"
            fontWeight="semibold"
            letterSpacing="wide"
            fontSize="xs"
            textTransform="uppercase"
          >
            <Flex alignItems="center">
              <TimeIcon mr={1} /> {duration ?? 'null'} menit &bull;{' '}
              <CalendarIcon mx={1} /> {year ?? 'null'}
            </Flex>
          </Box>

          <Box d="flex" mt="2" alignItems="center" align>
            {Array(10)
              .fill('')
              .map((_, i) => (
                <StarIcon
                  key={i}
                  color={i < rating ?? 0 ? 'teal.500' : 'gray.300'}
                />
              ))}
          </Box>
        </Box>
      </Box>
    </Flex>
  );
};

export default MovieCard;
