import React from 'react';
import {
  chakra,
  Box,
  Flex,
  Image,
  useColorModeValue,
  Link,
  Tooltip,
} from '@chakra-ui/react';
import { Link as ReachLink } from 'react-router-dom';
import { MdDevices } from 'react-icons/md';
const GameCard = ({ id, name, platform, image_url }) => {
  return (
    <Flex alignItems="center" justifyContent="center">
      <Box
        bg={useColorModeValue('gray.50', 'gray.700')}
        maxW="250px"
        maxH="500px"
        borderWidth="1px"
        rounded="lg"
        shadow="lg"
        transform="scale(1.0)"
        transition="0.2s ease-in-out"
        overflow="hidden"
        _hover={{
          transform: 'scale(1.05)',
        }}
      >
        <Link
          textDecoration="none"
          _hover={{ textDecoration: 'none' }}
          as={ReachLink}
          to={`/game/details/${id}`}
        >
          <Image
            src={image_url ?? 'null'}
            alt={name}
            roundedTop="lg"
            objectFit="cover"
            w="300px"
            h="300px"
          />
        </Link>

        <Box p="4">
          <Box fontWeight="semibold" as="h4" lineHeight="tight" isTruncated>
            <Link as={ReachLink} to={`/game/details/${id}`}>
              <Tooltip label={name ?? 'null'} aria-label="A tooltip">
                {name ?? 'null'}
              </Tooltip>
            </Link>
          </Box>

          <Flex alignItems="center" mt={1}>
            <MdDevices />
            <chakra.h1
              px={2}
              fontSize="sm"
              color="gray.500"
              fontWeight="semibold"
              isTruncated
            >
              {platform ?? 'null'}
            </chakra.h1>
          </Flex>
        </Box>
      </Box>
    </Flex>
  );
};

export default GameCard;
