import React, { useContext } from 'react';

import {
  chakra,
  Avatar,
  Flex,
  useColorModeValue,
  HStack,
  Button,
  useDisclosure,
  Link,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
} from '@chakra-ui/react';
import { Link as ReachLink, useHistory } from 'react-router-dom';
import { AiFillHome } from 'react-icons/ai';
import { MdMovie, MdGames } from 'react-icons/md';
import { ColorModeSwitcher } from '../../ColorModeSwitcher';
import { AuthContext } from '../../store/AuthContext';
import Cookies from 'js-cookie';

export default function Nav() {
  const { loginStatus, setLoginStatus } = useContext(AuthContext);
  const bg = useColorModeValue('gray.50', 'gray.900');
  const mobileNav = useDisclosure();

  let history = useHistory();

  const handleLogout = () => {
    setLoginStatus(false);
    Cookies.remove('email');
    Cookies.remove('user');
    Cookies.remove('token');
    history.push('/sign-in');
  };

  const auth = (
    <>
      <Link as={ReachLink} to="/sign-in">
        <Button
          display={{ base: 'none', md: 'inline-flex' }}
          fontSize={'sm'}
          fontWeight={700}
          variant="ghost"
        >
          Sign In
        </Button>
      </Link>
      <Link as={ReachLink} to="/sign-up">
        <Button
          display={{ base: 'none', md: 'inline-flex' }}
          fontSize={'sm'}
          fontWeight={700}
          px={4}
          color={'white'}
          bg={'blue.500'}
          href={'#'}
          _hover={{
            bg: 'blue.600',
          }}
        >
          Sign Up
        </Button>
      </Link>
    </>
  );

  const avatar = (
    <>
      <Menu>
        <MenuButton
          as={Button}
          rounded={'full'}
          variant={'link'}
          cursor={'pointer'}
          minW={0}
        >
          <Avatar bg="teal.500" size="sm" />
        </MenuButton>

        <MenuList>
          <Link as={ReachLink} to="/change-password">
            <MenuItem>Change Password</MenuItem>
          </Link>
          <MenuDivider />
          <MenuItem onClick={handleLogout}>Logout</MenuItem>
        </MenuList>
      </Menu>
    </>
  );

  return (
    <React.Fragment>
      <chakra.header bg={bg} w="full" px={6} py={4} shadow="md">
        <Flex alignItems="center" justifyContent="space-between" mx="auto">
          <HStack display="flex" spacing={3} alignItems="center">
            <HStack spacing={3} display={{ base: 'none', md: 'inline-flex' }}>
              <Link as={ReachLink} to="/">
                <Button variant="ghost" leftIcon={<AiFillHome />} size="md">
                  Home
                </Button>
              </Link>
              <Link as={ReachLink} to="/movies">
                <Button variant="ghost" leftIcon={<MdMovie />} size="md">
                  Movies
                </Button>
              </Link>
              <Link as={ReachLink} to="/games">
                <Button variant="ghost" leftIcon={<MdGames />} size="md">
                  Games
                </Button>
              </Link>
            </HStack>
          </HStack>
          <HStack
            spacing={3}
            display={mobileNav.isOpen ? 'none' : 'flex'}
            alignItems="center"
          >
            {loginStatus ? avatar : auth}
            <ColorModeSwitcher />
          </HStack>
        </Flex>
      </chakra.header>
    </React.Fragment>
  );
}
