import { ChakraProvider, theme, useToast } from '@chakra-ui/react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
import MovieDetails from '../components/Details/MovieDetails';
import Footer from '../components/Layout/Footer';
import Home from '../components/Pages/Home/Home';
import Nav from '../components/Layout/Nav';

import { AuthProvider } from '../store/AuthContext';
import { GamesProvider } from '../store/GamesContext';
import { MoviesProvider } from '../store/MoviesContext';
import Cookies from 'js-cookie';
import GameDetails from '../components/Details/GameDetails';
import MovieList from '../components/MovieList/MovieList';
import MovieForm from '../components/MovieForm/MovieForm';
import Movies from '../components/Pages/Movies/Movies';
import Games from '../components/Pages/Games/Games';
import SignIn from '../components/Auth/SignIn';
import ChangePassword from '../components/Auth/ChangePassword';
import SignUp from '../components/Auth/SignUp';
import GameList from '../components/GameList/GameList';
import GameForm from '../components/GameForm/GameForm';

const Routes = () => {
  const LoginRoute = ({ ...props }) => {
    if (Cookies.get('token') !== undefined) {
      return <Redirect to="/" />;
    } else {
      return <Route {...props} />;
    }
  };

  const RestrictedRoutes = ({ ...props }) => {
    if (Cookies.get('token') === undefined) {
      return <Redirect to="/sign-in" />;
    } else {
      return <Route {...props} />;
    }
  };

  return (
    <Router>
      <ChakraProvider theme={theme}>
        <AuthProvider>
          <MoviesProvider>
            <GamesProvider>
              <Nav />
              <Switch>
                <Route path="/" exact>
                  <Home />
                </Route>
                <Route path="/movies" exact>
                  <Movies />
                </Route>
                <Route path="/games" exact>
                  <Games />
                </Route>
                <LoginRoute path="/sign-in" exact>
                  <SignIn />
                </LoginRoute>
                <LoginRoute path="/sign-up" exact>
                  <SignUp />
                </LoginRoute>
                <RestrictedRoutes path="/change-password" exact>
                  <ChangePassword />
                </RestrictedRoutes>
                <Route path="/movie/details/:Id" exact>
                  <MovieDetails />
                </Route>
                <Route path="/game/details/:Id" exact>
                  <GameDetails />
                </Route>
                <RestrictedRoutes path="/movie/list" exact>
                  <MovieList />
                </RestrictedRoutes>
                <RestrictedRoutes path="/movie/add" exact>
                  <MovieForm />
                </RestrictedRoutes>
                <RestrictedRoutes path="/movie/edit/:Id" exact>
                  <MovieForm />
                </RestrictedRoutes>
                <RestrictedRoutes path="/game/list" exact>
                  <GameList />
                </RestrictedRoutes>
                <RestrictedRoutes path="/game/add" exact>
                  <GameForm />
                </RestrictedRoutes>
                <RestrictedRoutes path="/game/edit/:Id" exact>
                  <GameForm />
                </RestrictedRoutes>
              </Switch>
              <Footer />
            </GamesProvider>
          </MoviesProvider>
        </AuthProvider>
      </ChakraProvider>
    </Router>
  );
};

export default Routes;
