import { ArrowBackIcon } from '@chakra-ui/icons';
import {
  Heading,
  Box,
  Flex,
  VStack,
  GridItem,
  SimpleGrid,
  useColorModeValue,
  Stack,
  FormControl,
  FormLabel,
  Input,
  Button,
  Textarea,
  NumberInput,
  NumberInputField,
  chakra,
  Link,
} from '@chakra-ui/react';
import React, { useContext, useEffect } from 'react';
import { useParams, Link as ReachLink } from 'react-router-dom';

import { AuthContext } from '../../store/AuthContext';
import { MoviesContext } from '../../store/MoviesContext';
import Sidebar from '../Sidebar/Sidebar';

const MovieForm = () => {
  const { loginStatus } = useContext(AuthContext);
  const {
    input,
    setInput,
    currentId,
    setCurrentId,
    addMovieHandler,
    getMovieHandler,
    updateMovieHandler,
  } = useContext(MoviesContext);

  let { Id } = useParams();

  console.log(Id);

  useEffect(() => {
    if (Id !== undefined) {
      getMovieHandler(Id);
    } else {
      setInput({
        title: '',
        description: '',
        year: 1980,
        duration: 0,
        genre: '',
        rating: 0,
        review: '',
        image_url: '',
      });
      setCurrentId(null);
    }
  }, []);

  const handleChange = event => {
    let value = event.target.value;
    let name = event.target.name;

    setInput({ ...input, [name]: value });
  };

  const handleSubmit = e => {
    e.preventDefault();
    console.log(input);
    if (currentId === null) {
      addMovieHandler();
    } else {
      updateMovieHandler();
    }

    setInput({
      title: '',
      description: '',
      year: 1980,
      duration: 0,
      genre: '',
      rating: 0,
      review: '',
      image_url: '',
    });
    setCurrentId(null);
  };

  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Box p={8} mx="auto" w="1300px" data-aos="fade-down">
        <Box spacing={4}>
          <VStack spacing={4} align="stretch">
            <Heading fontSize={'4xl'}>Movie Form</Heading>
            <Link as={ReachLink} to="/movie/list">
              <Button
                leftIcon={<ArrowBackIcon />}
                colorScheme="blue"
                variant="outline"
                size="sm"
              >
                Back to list
              </Button>
            </Link>
            <Box mt={[10, 0]}>
              <SimpleGrid display="grid" columns="3" spacing="6">
                <GridItem mt={[5, null, 0]} colSpan="2">
                  <chakra.form
                    onSubmit={handleSubmit}
                    shadow="base"
                    rounded={[null, 'md']}
                    overflow={{ sm: 'hidden' }}
                  >
                    <Stack
                      p={6}
                      bg={useColorModeValue('white', 'gray.700')}
                      spacing={6}
                    >
                      <SimpleGrid columns={6} spacing={6}>
                        <FormControl as={GridItem} colSpan={[6, 3]} isRequired>
                          <FormLabel
                            htmlFor="title"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Title
                          </FormLabel>
                          <Input
                            type="text"
                            name="title"
                            id="title"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.title}
                          />
                        </FormControl>

                        <FormControl as={GridItem} colSpan={[6, 3]} isRequired>
                          <FormLabel
                            htmlFor="genre"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Genre
                          </FormLabel>
                          <Input
                            type="text"
                            name="genre"
                            id="genre"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.genre}
                          />
                        </FormControl>

                        <FormControl as={GridItem} colSpan={[6, 6]} isRequired>
                          <FormLabel
                            htmlFor="description"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Description
                          </FormLabel>
                          <Textarea
                            placeholder="Movie description"
                            name="description"
                            id="description"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.description}
                          />
                        </FormControl>

                        <FormControl as={GridItem} colSpan={[6, 6]} isRequired>
                          <FormLabel
                            htmlFor="review"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Review
                          </FormLabel>
                          <Textarea
                            onChange={handleChange}
                            placeholder="Movie review"
                            name="review"
                            id="review"
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.review}
                          />
                        </FormControl>

                        <FormControl
                          as={GridItem}
                          colSpan={[6, 6, null, 2]}
                          isRequired
                        >
                          <FormLabel
                            htmlFor="year"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Year
                          </FormLabel>
                          <NumberInput
                            min={1980}
                            max={2021}
                            value={input.year}
                            allowMouseWheel
                          >
                            <NumberInputField
                              name="year"
                              id="year"
                              onChange={handleChange}
                              mt={1}
                              shadow="md"
                              rounded="md"
                            />
                          </NumberInput>
                        </FormControl>

                        <FormControl
                          as={GridItem}
                          colSpan={[6, 3, null, 2]}
                          isRequired
                        >
                          <FormLabel
                            htmlFor="duration"
                            fontSize="sm"
                            fontWeight="md"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Duration
                          </FormLabel>
                          <NumberInput
                            size="md"
                            min={0}
                            defaultValue={0}
                            value={input.duration}
                            allowMouseWheel
                          >
                            <NumberInputField
                              name="duration"
                              id="duration"
                              onChange={handleChange}
                              mt={1}
                              shadow="md"
                              rounded="md"
                            />
                          </NumberInput>
                        </FormControl>

                        <FormControl
                          as={GridItem}
                          colSpan={[6, 3, null, 2]}
                          isRequired
                        >
                          <FormLabel
                            htmlFor="rating"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Rating
                          </FormLabel>
                          <NumberInput
                            min={0}
                            max={10}
                            defaultValue={0}
                            size="md"
                            value={input.rating}
                          >
                            <NumberInputField
                              name="rating"
                              id="rating"
                              onChange={handleChange}
                              mt={1}
                              shadow="md"
                              rounded="md"
                            />
                          </NumberInput>
                        </FormControl>

                        <FormControl as={GridItem} colSpan={6} isRequired>
                          <FormLabel
                            htmlFor="image_url"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Image URL
                          </FormLabel>
                          <Input
                            type="text"
                            name="image_url"
                            id="image_url"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.image_url}
                          />
                        </FormControl>
                      </SimpleGrid>
                    </Stack>
                    <Box
                      px={{ base: 4, sm: 6 }}
                      py={3}
                      bg={useColorModeValue('gray.50', 'gray.900')}
                      textAlign="right"
                    >
                      <Button
                        type="submit"
                        colorScheme="teal"
                        _focus={{ shadow: '' }}
                        fontWeight="md"
                      >
                        Save
                      </Button>
                    </Box>
                  </chakra.form>
                </GridItem>
              </SimpleGrid>
            </Box>
          </VStack>
        </Box>
      </Box>
    </Flex>
  );
};

export default MovieForm;
