import React, { useState, createContext, useEffect, useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { useToast } from '@chakra-ui/react';
import {
  addGame,
  deleteGame,
  getGame,
  getGames,
  updateGame,
} from '../services/gamesApi';

export const GamesContext = createContext();

export const initialGameInput = {
  name: '',
  genre: '',
  singlePlayer: false,
  multiplayer: false,
  platform: '',
  release: '2000',
  image_url: '',
};

export const GamesProvider = props => {
  const [games, setGames] = useState([]);
  const [fetchStatus, setFetchStatus] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [currentId, setCurrentId] = useState(null);
  const [input, setInput] = useState(initialGameInput);
  let history = useHistory();
  const toast = useToast();

  const getGameList = useCallback(async () => {
    const data = await getGames();
    setGames(data);
    setIsLoading(false);
  }, [getGames]);

  useEffect(() => {
    if (fetchStatus) {
      getGameList();
      setFetchStatus(false);
      setIsLoading(true);
    }
  }, [fetchStatus]);

  const handleChange = event => {
    let value = event.target.value;
    let name = event.target.name;
    let gameType = ['singlePlayer', 'multiplayer'];
    if (gameType.indexOf(name) === -1) setInput({ ...input, [name]: value });
    else setInput({ ...input, [name]: !input[name] });
  };

  const addGameHandler = () => {
    addGame({
      name: input.name,
      genre: input.genre,
      singlePlayer: input.singlePlayer,
      multiplayer: input.multiplayer,
      platform: input.platform,
      release: input.release,
      image_url: input.image_url,
    }).then(() => {
      setFetchStatus(true);
      history.push('/game/list');
      toast({
        title: 'Success',
        description: `Successfully added new game`,
        status: 'success',
        duration: 3000,
        isClosable: true,
        position: 'top',
      });
    });
  };

  const updateGameHandler = () => {
    updateGame(currentId, {
      name: input.name,
      genre: input.genre,
      singlePlayer: input.singlePlayer,
      multiplayer: input.multiplayer,
      platform: input.platform,
      release: input.release,
      image_url: input.image_url,
    }).then(() => {
      setFetchStatus(true);
      history.push('/game/list');
      toast({
        title: 'Success',
        description: `Game updated`,
        status: 'success',
        duration: 3000,
        isClosable: true,
        position: 'top',
      });
    });
  };

  const getGameHandler = id => {
    getGame(id).then(res => {
      setInput({
        id: res.id,
        name: res.name ?? 'null',
        genre: res.genre ?? 'null',
        singlePlayer: res.singlePlayer ?? false,
        multiplayer: res.multiplayer ?? false,
        platform: res.platform ?? 'null',
        release: res.release ?? 0,
        image_url: res.image_url ?? 'null',
      });
      setCurrentId(res.id);
      console.log(res);
      console.log(currentId);
    });
  };

  const deleteGameHandler = id => {
    deleteGame(id).then(() => {
      toast({
        title: 'Success',
        description: `Selected movie successfully removed`,
        status: 'success',
        duration: 3000,
        isClosable: true,
        position: 'top',
      });
      setFetchStatus(true);
    });
  };

  return (
    <GamesContext.Provider
      value={{
        games,
        setGames,
        isLoading,
        setIsLoading,
        setFetchStatus,
        fetchStatus,
        handleChange,
        currentId,
        setCurrentId,
        input,
        setInput,
        addGameHandler,
        deleteGameHandler,
        getGameHandler,
        updateGameHandler,
        getGameList,
      }}
    >
      {props.children}
    </GamesContext.Provider>
  );
};
