import React, { useState, createContext, useEffect, useCallback } from 'react';
import { useToast } from '@chakra-ui/react';
import { useHistory } from 'react-router-dom';
import {
  addMovie,
  deleteMovie,
  getMovie,
  getMovies,
  updateMovie,
} from '../services/moviesApi';

export const MoviesContext = createContext();

export const MoviesProvider = props => {
  const [movies, setMovies] = useState([]);
  const [fetchStatus, setFetchStatus] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [currentId, setCurrentId] = useState(null);
  const [input, setInput] = useState({
    title: '',
    description: '',
    year: 1980,
    duration: 0,
    genre: '',
    rating: 0,
    review: '',
    image_url: '',
  });
  let history = useHistory();
  const toast = useToast();

  const getMovieList = useCallback(async () => {
    const data = await getMovies();
    setMovies(data);
    setIsLoading(false);
  }, [getMovies]);

  useEffect(() => {
    if (fetchStatus) {
      getMovieList();
      setFetchStatus(false);
      setIsLoading(true);
    }
  }, [fetchStatus]);

  const handleChange = event => {
    let value = event.target.value;
    let name = event.target.name;

    setInput({ ...input, [name]: value });
  };

  const addMovieHandler = () => {
    addMovie({
      title: input.title,
      description: input.description,
      year: parseInt(input.year),
      duration: parseInt(input.duration),
      genre: input.genre,
      rating: parseInt(input.rating),
      review: input.review,
      image_url: input.image_url,
    }).then(() => {
      setFetchStatus(true);
      history.push('/movie/list');
      toast({
        title: 'Success',
        description: `Successfully added new movie`,
        status: 'success',
        duration: 3000,
        isClosable: true,
        position: 'top',
      });
    });
  };

  const updateMovieHandler = () => {
    updateMovie(currentId, {
      title: input.title,
      description: input.description,
      year: parseInt(input.year),
      duration: parseInt(input.duration),
      genre: input.genre,
      rating: parseInt(input.rating),
      review: input.review,
      image_url: input.image_url,
    })
      .then(() => {
        setFetchStatus(true);
        history.push('/movie/list');
        toast({
          title: 'Success',
          description: `Movie updated`,
          status: 'success',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
      })
      .catch(() => {
        toast({
          title: 'Error',
          description: `Data not found`,
          status: 'error',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
        setFetchStatus(true);
      });
  };

  const deleteMovieHandler = id => {
    deleteMovie(id)
      .then(() => {
        toast({
          title: 'Success',
          description: `Selected movie successfully removed`,
          status: 'success',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
        setFetchStatus(true);
      })
      .catch(() => {
        toast({
          title: 'Error',
          description: `Data not found`,
          status: 'error',
          duration: 3000,
          isClosable: true,
          position: 'top',
        });
        setFetchStatus(true);
      });
  };

  const getMovieHandler = id => {
    getMovie(id).then(res => {
      setInput({
        id: res.id,
        title: res.title ?? 'null',
        description: res.description ?? 'null',
        year: res.year ?? 0,
        duration: res.duration ?? 0,
        genre: res.genre ?? 'null',
        rating: res.rating ?? 0,
        review: res.review ?? 'null',
        image_url: res.image_url ?? 'null',
      });
      setCurrentId(res.id);
      console.log(res);
      console.log(currentId);
    });
  };

  return (
    <MoviesContext.Provider
      value={{
        movies,
        setMovies,
        isLoading,
        setIsLoading,
        fetchStatus,
        setFetchStatus,
        getMovieList,
        input,
        setInput,
        currentId,
        setCurrentId,
        handleChange,
        addMovieHandler,
        deleteMovieHandler,
        getMovieHandler,
        updateMovieHandler,
      }}
    >
      {props.children}
    </MoviesContext.Provider>
  );
};
