import { ArrowBackIcon } from '@chakra-ui/icons';
import {
  Heading,
  Box,
  Flex,
  VStack,
  GridItem,
  SimpleGrid,
  useColorModeValue,
  Stack,
  FormControl,
  FormLabel,
  Input,
  Button,
  Checkbox,
  NumberInput,
  NumberInputField,
  chakra,
  Link,
} from '@chakra-ui/react';
import React, { useContext, useEffect } from 'react';
import { useParams, Link as ReachLink } from 'react-router-dom';

import { AuthContext } from '../../store/AuthContext';
import { GamesContext, initialGameInput } from '../../store/GamesContext';
import Sidebar from '../Sidebar/Sidebar';

const GameForm = () => {
  const { loginStatus } = useContext(AuthContext);
  const {
    input,
    setInput,
    currentId,
    setCurrentId,
    addGameHandler,
    updateGameHandler,
    getGameHandler,
    handleChange,
  } = useContext(GamesContext);

  let { Id } = useParams();

  console.log(Id);

  useEffect(() => {
    if (Id !== undefined) {
      getGameHandler(Id);
    } else {
      setInput(initialGameInput);
      setCurrentId(null);
    }
  }, []);

  const handleSubmit = e => {
    e.preventDefault();
    console.log(input);
    if (currentId === null) {
      addGameHandler();
    } else {
      updateGameHandler();
    }

    setInput(initialGameInput);
    setCurrentId(null);
  };

  return (
    <Flex>
      {loginStatus && <Sidebar />}
      <Box p={8} mx="auto" w="1300px" data-aos="fade-down">
        <Box spacing={4}>
          <VStack spacing={4} align="stretch">
            <Heading fontSize={'4xl'}>Game Form</Heading>
            <Link as={ReachLink} to="/game/list">
              <Button
                leftIcon={<ArrowBackIcon />}
                colorScheme="blue"
                variant="outline"
                size="sm"
              >
                Back to list
              </Button>
            </Link>
            <Box mt={[10, 0]}>
              <SimpleGrid display="grid" columns="3" spacing="6">
                <GridItem mt={[5, null, 0]} colSpan="2">
                  <chakra.form
                    onSubmit={handleSubmit}
                    shadow="base"
                    rounded={[null, 'md']}
                    overflow={{ sm: 'hidden' }}
                  >
                    <Stack
                      p={6}
                      bg={useColorModeValue('white', 'gray.700')}
                      spacing={6}
                    >
                      <SimpleGrid columns={6} spacing={6}>
                        <FormControl as={GridItem} colSpan={[6, 3]} isRequired>
                          <FormLabel
                            htmlFor="name"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Name
                          </FormLabel>
                          <Input
                            type="text"
                            name="name"
                            id="name"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.name}
                          />
                        </FormControl>

                        <FormControl as={GridItem} colSpan={[6, 3]} isRequired>
                          <FormLabel
                            htmlFor="genre"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Genre
                          </FormLabel>
                          <Input
                            type="text"
                            name="genre"
                            id="genre"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            rounded="md"
                            value={input.genre}
                          />
                        </FormControl>

                        <FormControl as={GridItem} colSpan={[6, 3]} isRequired>
                          <FormLabel
                            htmlFor="platform"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Platform
                          </FormLabel>
                          <Input
                            type="text"
                            name="platform"
                            id="platform"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            w="full"
                            rounded="md"
                            value={input.platform}
                          />
                        </FormControl>

                        <FormControl
                          as={GridItem}
                          colSpan={[6, 6, null, 2]}
                          isRequired
                        >
                          <FormLabel
                            htmlFor="release"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Release
                          </FormLabel>
                          <NumberInput
                            defaultValue={2000}
                            min={2000}
                            max={2021}
                            value={input.release}
                            allowMouseWheel
                          >
                            <NumberInputField
                              name="release"
                              id="release"
                              onChange={handleChange}
                              mt={1}
                              shadow="md"
                              w="full"
                              rounded="md"
                            />
                          </NumberInput>
                        </FormControl>

                        <FormControl as={GridItem} colSpan={6} isRequired>
                          <FormLabel
                            htmlFor="image_url"
                            fontSize="sm"
                            color={useColorModeValue('gray.700', 'gray.50')}
                          >
                            Image URL
                          </FormLabel>
                          <Input
                            type="text"
                            name="image_url"
                            id="image_url"
                            onChange={handleChange}
                            mt={1}
                            shadow="md"
                            size="md"
                            w="full"
                            rounded="md"
                            value={input.image_url}
                          />
                        </FormControl>

                        <FormControl as={GridItem} isRequired>
                          <Checkbox
                            name="singlePlayer"
                            id="singlePlayer"
                            shadow="sm"
                            size="md"
                            mt={1}
                            onChange={handleChange}
                            isChecked={input.singlePlayer}
                          >
                            Singleplayer
                          </Checkbox>
                        </FormControl>
                        <FormControl as={GridItem} isRequired>
                          <Checkbox
                            name="multiplayer"
                            id="multiplayer"
                            shadow="sm"
                            size="md"
                            mt={1}
                            onChange={handleChange}
                            isChecked={input.multiplayer}
                          >
                            Multiplayer
                          </Checkbox>
                        </FormControl>
                      </SimpleGrid>
                    </Stack>
                    <Box
                      px={{ base: 4, sm: 6 }}
                      py={3}
                      bg={useColorModeValue('gray.50', 'gray.900')}
                      textAlign="right"
                    >
                      <Button
                        type="submit"
                        colorScheme="teal"
                        _focus={{ shadow: '' }}
                        fontWeight="md"
                      >
                        Save
                      </Button>
                    </Box>
                  </chakra.form>
                </GridItem>
              </SimpleGrid>
            </Box>
          </VStack>
        </Box>
      </Box>
    </Flex>
  );
};

export default GameForm;
