import {
  Heading,
  Box,
  Flex,
  Divider,
  VStack,
  Grid,
  GridItem,
  Spinner,
  Link,
  Button,
} from '@chakra-ui/react';
import { ArrowForwardIcon } from '@chakra-ui/icons';
import { useContext, useEffect } from 'react';
import { Link as ReachLink } from 'react-router-dom';
import { GamesContext } from '../../../store/GamesContext';
import GameCard from '../../Card/GameCard';

const GameSection = () => {
  const { games, isLoading, setFetchStatus } = useContext(GamesContext);
  useEffect(() => {
    setFetchStatus(true);
  }, []);

  const sortGame = games.sort((a, b) => (a.created_at < b.created_at ? 1 : -1));

  return (
    <Box>
      <VStack spacing={4} align="stretch">
        <Flex alignItems="center" justifyContent="space-between">
          <Heading fontSize={'4xl'}>Latest Games</Heading>
          <Link as={ReachLink} to="/games">
            <Button
              rightIcon={<ArrowForwardIcon />}
              colorScheme="blue"
              variant="outline"
            >
              See more
            </Button>
          </Link>
        </Flex>
        <Divider borderColor="gray.200" />
        {isLoading ? (
          <Box textAlign="center">
            <Spinner size="xl" />
          </Box>
        ) : (
          <Box>
            <Grid
              templateColumns={{
                base: 'repeat(2, 1fr)',
                md: 'repeat(3, 1fr)',
                lg: 'repeat(4, 1fr)',
                xl: 'repeat(5, minmax(0, 1fr))',
              }}
              gap={3}
              autoFlow="row"
            >
              {sortGame?.map(
                (game, index) =>
                  index < 5 && (
                    <GridItem key={game.id}>
                      <GameCard
                        id={game.id}
                        name={game.name}
                        image_url={game.image_url}
                        release={game.release}
                        platform={game.platform}
                        genre={game.genre}
                        singlePlayer={game.singlePlayer}
                        multiplayer={game.multiplayer}
                      />
                    </GridItem>
                  )
              )}
            </Grid>
          </Box>
        )}
      </VStack>
    </Box>
  );
};

export default GameSection;
