import {
  Heading,
  Box,
  Flex,
  Button,
  Divider,
  VStack,
  Grid,
  GridItem,
  Link,
  Spinner,
} from '@chakra-ui/react';
import { useContext, useEffect } from 'react';
import { Link as ReachLink } from 'react-router-dom';
import { ArrowForwardIcon } from '@chakra-ui/icons';
import { MoviesContext } from '../../../store/MoviesContext';
import MovieCard from '../../Card/MovieCard';

const MovieSection = () => {
  const { movies, isLoading, setFetchStatus } = useContext(MoviesContext);

  useEffect(() => {
    setFetchStatus(true);
  }, []);

  const sortMovie = movies.sort((a, b) =>
    a.created_at < b.created_at ? 1 : -1
  );

  return (
    <Box spacing={4}>
      <VStack spacing={4} align="stretch">
        <Flex alignItems="center" justifyContent="space-between">
          <Heading fontSize={'4xl'}>Latest Movies</Heading>
          <Link as={ReachLink} to="/movies">
            <Button
              rightIcon={<ArrowForwardIcon />}
              colorScheme="blue"
              variant="outline"
            >
              See more
            </Button>
          </Link>
        </Flex>
        <Divider borderColor="gray.200" />
        {isLoading ? (
          <Box textAlign="center">
            <Spinner size="xl" />
          </Box>
        ) : (
          <Box>
            <Grid
              templateColumns={{
                base: 'repeat(2, 1fr)',
                md: 'repeat(3, 1fr)',
                lg: 'repeat(4, 1fr)',
                xl: 'repeat(5, minmax(0, 1fr))',
              }}
              gap={3}
              autoFlow="row"
            >
              {sortMovie?.map(
                (movie, index) =>
                  index < 5 && (
                    <GridItem key={movie.id}>
                      <MovieCard
                        id={movie.id}
                        title={movie.title}
                        image_url={movie.image_url}
                        year={movie.year}
                        duration={movie.duration}
                        genre={movie.genre}
                        rating={movie.rating}
                      />
                    </GridItem>
                  )
              )}
            </Grid>
          </Box>
        )}
      </VStack>
    </Box>
  );
};

export default MovieSection;
