import axios from 'axios';
import Cookies from 'js-cookie';

const API_ENDPOINT = `https://backendexample.sanbersy.com/api`;

export async function authRegister(userData) {
  const result = await axios.post(`${API_ENDPOINT}/register`, userData);
  const axiosResult = result;

  return axiosResult;
}

export async function authLogin(userData) {
  const result = await axios.post(`${API_ENDPOINT}/user-login`, userData);
  const axiosResult = result;

  return axiosResult;
}

export async function authChangePassword(userData) {
  const result = await axios.post(`${API_ENDPOINT}/change-password`, userData, {
    headers: { Authorization: 'Bearer ' + Cookies.get('token') },
  });
  const axiosResult = result;

  return axiosResult;
}
